.button_container {
	width: 280px;
	margin: 0 auto;
	margin-top: 150px;
}

.pop_button {
	padding: 10px 20px;
	border-radius: 5px;
	background-color: brown;
	color: #ffffff;
	font-size: 22px;
}

.pop_container {
	position: absolute;
	display: flex;
	flex-direction: column;
	min-width: 150px;
	max-width: 300px;
	border: 1px #b4b4b4 solid;
	border-radius: 2px;
	background-color: #ffffff;
}

.pop_title {
	background-color: #e6e6e6;
	font-weight: bold;
	padding: 5px 10px;
	font-size: 18px;
}

.pop_text {
	padding: 5px 13px;
	font-size: 16px;
}

body {
	height: 1500px;
}